.header {
	position: fixed;
	z-index: 10;
	left: 0;
	right: 0;
	top: 0;
	display: flex;
	align-items: center;
	background-color: $color-black;
	height: var(--responsive-indent);

	&__contacts {
		display: flex;
		align-items: center;
		justify-content: center;

		a {
			margin-right: 25px;
			font-weight: 700;
		}

		&-soc {
			display: block;
			width: 21px;
			height: 21px;
			line-height: 1;

			svg use {
				fill: $color-yellow;
			}
		}
	}

	&__logo {
		display: block;
		width: 100%;
		height: 37px;
		transition: $duration-normal;
	}

	&__icons {
		display: flex;
		position: relative;
		justify-content: flex-end;
		align-items: center;

		.header__lk a {
			width: 26px;
			height: 27px;
			margin-right: 25px;
			display: inline-block;
			line-height: 1;
		}
	}

	&__menu {

		.header__list {
			background-color: $color-black;
		}
	}

	&__list {
		width: 100%;
		text-align: center;
	}

	&__link {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: left;
		margin: 0 auto;
		padding: 10px 30px;
		font-weight: 700;
		font-size: 16px;
		line-height: 25px;
		text-align: left;
		color: rgba($color-black, 0.7);
		background-color: $color-white;

		&:not(:last-child) {
			border-bottom: 1px solid $color-gray;
		}

		&:first-child {
			border-top: 1px solid $color-gray;
		}

		@include hover {
			color: $color-black;
		}

		@include active {
			color: $color-black;
		}
	}

	&__burger {
		position: relative;
		width: 26px;
		// z-index: 100;
		// flex-direction: column;
		// margin: (-6.5px / 2) 0;
		// margin-left: auto;
		// width: 39px;
		// color: $color-black; 
	}
}

.content-center {
	display: flex;
	align-items: center;
	justify-content: center;
}

.burger__box {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	height: 100%;
}

.burger__btn {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	cursor: pointer;
}

.burger__btn span {
	display: block;
	margin: 6px 0;
	width: 100%;
	height: 2px;
	border-radius: 1px;
	background: $color-yellow-hover;
	transition: all .3s;
	position: relative;
}

.header__burger.is-active .burger__btn span:nth-child(1) {
	animation: ease .7s top forwards;
}

.header__burger .burger__btn span:nth-child(1) {
	animation: ease .7s top-2 forwards;
}

.header__burger.is-active .burger__btn span:nth-child(2) {
	animation: ease .7s scaled forwards;
}

.header__burger .burger__btn span:nth-child(2) {
	animation: ease .7s scaled-2 forwards;
}

.header__burger.is-active .burger__btn span:nth-child(3) {
	animation: ease .7s bottom forwards;
}

.header__burger .burger__btn span:nth-child(3) {
	animation: ease .7s bottom-2 forwards;
}

@keyframes top {
	0% {
		top: 0;
		transform: rotate(0);
	}

	50% {
		top: 8px;
		transform: rotate(0);
	}

	100% {
		top: 8px;
		transform: rotate(45deg);
	}
}

@keyframes top-2 {
	0% {
		top: 8px;
		transform: rotate(45deg);
	}

	50% {
		top: 8px;
		transform: rotate(0deg);
	}

	100% {
		top: 0;
		transform: rotate(0deg);
	}
}

@keyframes bottom {
	0% {
		bottom: 0;
		transform: rotate(0);
	}

	50% {
		bottom: 8px;
		transform: rotate(0);
	}

	100% {
		bottom: 8px;
		transform: rotate(135deg);
	}
}

@keyframes bottom-2 {
	0% {
		bottom: 8px;
		transform: rotate(135deg);
	}

	50% {
		bottom: 8px;
		transform: rotate(0);
	}

	100% {
		bottom: 0;
		transform: rotate(0);
	}
}

@keyframes scaled {
	50% {
		transform: scale(0);
	}

	100% {
		transform: scale(0);
	}
}

@keyframes scaled-2 {
	0% {
		transform: scale(0);
	}

	50% {
		transform: scale(0);
	}

	100% {
		transform: scale(1);
	}
}

.mm-ocd__content {
	background-color: transparent;
}

.mm-spn.mm-spn--dark {

	color: $color-yellow-hover;
	// background: $color-black;
	background: transparent;
}

.mm-spn.mm-spn--navbar:after {
	content: none;
}

.mm-spn a {
	font-size: 1.7rem;
	border: 0px solid;
	display: flex;
	align-items: center;
	padding: 15px 45px;
}

.mm-spn li:after {
	content: none;
}

.header__link:first-child {
	border: 0px solid;
}

.mm-spn.mm-spn--navbar ul {
	top: 0;
}

.mm-ocd {
	top: var(--responsive-indent);
}

.has-hover .header__link:not([disabled]):hover {
	color: inherit;
}

.mm-spn.mm-spn--navbar ul:before {
	border: 0px solid;
}