.home {
	margin-top: var(--responsive-indent);
	padding: 0;

	font-family: 'Open Sans';
	font-size: 24px;

	color: #000000;

	.block-title {
		font-weight: 700;
		font-size: 40px;
		line-height: 54px;
		text-align: center;
		color: #000000;

		&_lefted {
			text-align: left;
		}
	}

	.block-info {
		width: 90%;
		font-weight: 400;
		font-size: 24px;
		line-height: 33px;
		text-align: center;
		color: #000000;
	}

	.btn-wrap {
		display: inline-block;
		background: #F5E400;
		border-radius: 1000px;
		font-style: normal;
		font-weight: 600;
		font-size: 15px;
		line-height: 112.18%;
		letter-spacing: 0.03em;
		text-transform: uppercase;
		color: #000000;
		height: 61px;
		text-align: center;
		border: none;
		cursor: pointer;

		&.btn_small {
			width: auto;
			height: 40px;
			padding: 7px 25px;

			a {
				font-weight: 700;
				font-size: 15px;
				line-height: 25px;
				letter-spacing: 0.03em;
				text-transform: uppercase;
				color: #000000;
			}

		}
	}

	.home-slider {
		border-radius: 0 0 16px 16px;
		overflow: hidden;
	}

	.swiper-controls {
		display: none;
	}

	.swiper-slide {
		position: relative;

		img {
			width: 100%;
			height: 100%;
			max-height: 100vh;
			object-fit: cover;
			object-position: bottom;
		}
	}

	.banners__info {
		display: flex;
		flex-wrap: nowrap;

		&-wrap {
			position: absolute;
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;

			&>.container {
				height: 100%;
				display: flex;
				align-items: center;
			}

			a.btn {
				color: #000000;
				display: flex;
				align-items: center;
				height: 100%;
				padding: 0 25px;
			}
		}

		.text1,
		.text2 {
			font-weight: 700;
			font-size: 40px;
			line-height: 54px;
		}

		.text2 {
			margin-top: 30px;
		}

		.text2+.btn-wrap {
			margin-top: 67px;
		}


		@media (max-width: 575.98px) {
			.videoButton a {
				width: 40px;
			}
		}

		.videoButton a svg {
			width: 100%;
		}
	}

	.videoButton {
		position: absolute;
		right: 0;
		top: 17%;
		height: 63px;
		width: 75px;
		animation: 1.9s pulse infinite;

		a {
			position: relative;
			overflow: hidden;
			border-radius: 19%;
			animation-delay: 1s;
			height: max-content;
			width: 70px;
			opacity: 1;
			transition: 0.5s ease opacity;

			&:before {
				content: "";
				position: absolute;
				top: 0;
				height: 56px;
				animation: 1.5s swoosh infinite;
				animation-delay: 0.5s;
				transform: skewX(-15deg);
				background: rgba(255, 255, 255, 0.2);
				will-change: auto;
				opacity: 0;
				z-index: 1;
			}

			svg {
				transition: 0.5s ease transform;
				transform: scale(1);
				transform-origin: center center;
			}

			&:hover {
				opacity: 0.8;

				svg {
					transform: scale(0.94);
				}
			}
		}
	}

	@-webkit-keyframes swoosh {
		20% {
			left: -80%;
			opacity: 0;
			width: 5px;
		}

		50% {
			opacity: 0.8;
			width: 50px;
		}

		100% {
			left: 100%;
			opacity: 0;
			width: 5px;
		}
	}

	@keyframes swoosh {
		20% {
			left: -80%;
			opacity: 0;
			width: 5px;
		}

		50% {
			opacity: 0.8;
			width: 50px;
		}

		100% {
			left: 100%;
			opacity: 0;
			width: 5px;
		}
	}

	@-webkit-keyframes pulse {
		0% {
			transform: scale(1);
		}

		25% {
			transform: scale(1.1);
		}

		50% {
			transform: scale(1);
		}

		100% {
			transform: scale(1);
		}
	}

	@keyframes pulse {
		0% {
			transform: scale(1);
		}

		25% {
			transform: scale(1.1);
		}

		50% {
			transform: scale(1);
		}

		100% {
			transform: scale(1);
		}
	}

	.directions,
	.kids-slider {
