		padding-top: 110px;
		padding-bottom: 0px;

		.block-info {
			max-width: 870px;
			margin: 0 auto;
			margin-bottom: 40px;
		}

		.col-lg-4 {
			width: calc(100%*.33333 - var(--gutter)*2 - 0.1px);
		}

		&__card {
			cursor: default;
			margin-left: var(--gutter);
			margin-right: var(--gutter);
			background: #FFFFFF;
			box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
			margin-bottom: 40px;
			padding: 40px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			border-radius: 16px;
			transition: .3s;
			border: 1px solid transparent;

			&-title {
				text-align: center;
				font-weight: 700;
				font-size: 17px;
				line-height: 114%;

