					background-color: $color-yellow;
					border-radius: 16px;
					margin: 13px 0 30px;
					min-height: 37px;
					padding: 20px 35px;
					font-weight: 400;
					font-size: 16px;
					line-height: 19px;
					display: flex;
					justify-content: center;
					align-items: center;

					&::before {
