	.video-presentation {
		padding-top: 100px;
		padding-bottom: 100px;

		.present-vid {
			position: relative;

			&__image {}

			&__signature {
				position: absolute;
				bottom: 0;
				left: 0;
				height: 107px;
				width: 566px;
				display: flex;
				justify-content: center;
				align-items: center;
				padding-left: 190px;
				background: url("../images/present-vid__sign.svg") no-repeat center center/cover transparent;
				font-weight: 400;
				font-size: 27px;
				line-height: 112.18%;
				transition: .3s;

				&:hover {
					left: 10px;
				}
			}
		}
	}

	.book-camp,
	.book-choise,
	.openform {
		padding-top: 70px;
		padding-bottom: 115px;

		background-color: #F7F7F7;

		&__img {
			position: relative;
		}

		&__sticker {
			position: absolute;
			width: 164px;
			height: 67px;
			right: 180px;
			top: 4px;
			background: #F5E400;
			border-radius: 16px;
			transform: rotate(-14.43deg);
			font-weight: 700;
			font-size: 18px;
			line-height: 1.1em;
			padding: 10px 15px;
			display: flex;
			justify-content: center;
			align-items: center;
		}

		&__information {
			padding-left: 15px;

			.block-title {
				margin-bottom: 37px;
			}

			.book-camp__list {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;

				&-item {
					flex: 0 0 calc(50% - 9px);
					background: url("../images/icons/book-camp__flag.svg") no-repeat 40px center/ 50px #FFFFFF;
					border-radius: 16px;
					padding: 30px;
					padding-left: 20%;
					margin-bottom: 17px;
					margin-bottom: 17px;

					font-weight: 400;
					font-size: 23px;
					line-height: 1.2em;
					color: #000000;

					&_star {
						background-image: url("../images/icons/book-camp__star.svg");
					}
				}

				&-item:nth-child(2),
				&-item:nth-child(3) {
					background-size: 30px;
				}

				&+.btn-wrap {
					margin-top: 20px;
				}
			}
		}
	}

	.wehave {
		padding-top: 110px;
		padding-bottom: 25px;

		&__carousel {
			width: 90%;
			margin-top: 47px;

			&-item {
				position: relative;
				border-radius: 16px;
				overflow: hidden;
				cursor: pointer;
				display: block;
				height: auto;
				transition: .3s;

				&:hover {
					box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
				}

			}

			&-link {
				padding-top: 100%;
				display: inline-block;
				height: 0;
				width: 100%;
				position: relative;
				vertical-align: top;
				padding-top: 100%;

				.image-flex-center {
					position: absolute;
					left: 0;
					top: 0;
					display: flex;
					width: 100%;
					height: 100%;
					align-items: center;
					align-content: center;
					justify-content: center;
					flex: 0 0 auto;
					-webkit-box-orient: vertical;
					flex-direction: column;
				}

				.image-flex-center img {
					position: static;
					top: initial;
					left: initial;
					transform: none;
				}
			}

			&-footer {
				position: absolute;
				bottom: 0;
				left: 0;
				height: 110px;
				width: 100%;
				background-color: rgba(0, 0, 0, 0.75);
				z-index: 2;
				padding: 0;
				color: #fff;
				display: flex;
				align-items: center;
				justify-content: center;
				text-align: center;
				line-height: 1.2;
				margin: 0;

				h3 {
					position: relative;
					font-size: 1.3rem;

					&:after {
						content: url("../images/icon-arrow.svg");
						width: 9px;
						height: 20px;
						position: absolute;
						top: 2px;
						left: 100%;
						margin-left: 13px;
						-webkit-animation: 2s ease arrowSwing infinite;
						animation: 2s ease arrowSwing infinite;
					}
				}

				@media only screen and (max-width: 990px) {
					h3 {
						font-size: 1.1rem;
					}
				}
			}

			@-webkit-keyframes arrowSwing {
				0% {
					transform: translateX(0px);
				}

				50% {
					transform: translateX(4px);
				}

				100% {
					transform: translateX(0px);
				}
			}

			@keyframes arrowSwing {
				0% {
					transform: translateX(0px);
				}

				50% {
					transform: translateX(4px);
				}

				100% {
					transform: translateX(0px);
				}
			}

		}
	}

