	.gallery-slider {
		padding-top: 110px;

		.gallery__carousel {
			margin-top: 70px;
			margin-bottom: 70px;

			&-item {
				border-radius: 16px;
				overflow: hidden;
			}

			&-link {
				padding-top: 100%;
				display: inline-block;
				height: 0;
				width: 100%;
				position: relative;
				vertical-align: top;
				padding-top: 78%;

				.image-flex-center {
					position: absolute;
					left: 0;
					top: 0;
					display: flex;
					width: 100%;
					height: 100%;
					align-items: center;
					align-content: center;
					justify-content: center;
					flex: 0 0 auto;
					-webkit-box-orient: vertical;
					flex-direction: column;
				}

				.image-flex-center img {
					position: static;
					top: initial;
					left: initial;
					transform: none;
				}
			}
