	.openform {


		.popup {
			background-color: #F5E400;
			max-width: 100%;
			box-shadow: 0 4px 20px rgba(0, 0, 0, .2);

			position: relative;
			margin: 0 auto;
			padding: 60px 30px 50px 30px;
			border-radius: 16px;
			margin-top: 70px;
		}

		.form__input input {
			width: 100%;
			border: 2px solid transparent;
			border-radius: 30px;
			padding: 10px 25px;
		}

		.popup__title {
			font-size: 30px;
			font-weight: 700;
			margin-bottom: 30px;
			line-height: 1.2
		}

		.form__input {
			position: relative;
			margin-bottom: 20px
		}

		.form__input.required input {
			border-color: #000
		}

		.form__input.required::before {
			content: "обязательное поле";
			display: inline-block;
			font-size: 12px;
			color: #ccc;
			position: absolute;
			right: 30px;
			top: 50%;
			-webkit-transform: translateY(-50%);
			-ms-transform: translateY(-50%);
			transform: translateY(-50%)
		}

		.form__input input {
			width: 100%;
			border: 2px solid transparent;
			border-radius: 30px;
			padding: 10px 25px
		}

		.form__logo {
			display: flex;
			align-items: center;
			justify-content: center
		}

		.form__logo img {
			width: 130px;
		}

		.form__logo .sprite {
			width: 180px;
			height: 91px
		}

		.form__check {
			margin-bottom: 20px
		}

		.form__check label {
			cursor: pointer
		}

		.form__check input {
			display: none
		}

		.form__check input:checked+span:before {
			background-color: #000;
			background-image: url("../images/icons/chekbox.svg")
		}

		.form__check span {
			font-size: 14px;
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
			-webkit-align-items: center;
			-ms-flex-align: center;
			align-items: center;
			line-height: 1.3;
			flex-wrap: wrap;
		}

		.form__check a {
			color: #000000;
			text-decoration: underline;
			margin: 0 3px;
		}

		.form__check span::before {
			content: "";
			width: 24px;
			height: 24px;
			border-radius: 2px;
			border: 1px solid #000;
			display: inline-block;
			margin-right: 15px;
			padding: 5px;
			-webkit-flex-shrink: 0;
			-ms-flex-negative: 0;
			flex-shrink: 0;
			background-repeat: no-repeat;
			background-size: 18px;
			background-position: center
		}

		.form__submit {
			margin-top: 20px
		}

		.bt.bt-border {
			border: 1px solid #000;
			background-color: transparent;
			padding: 20px 70px;
			border-radius: 50px;

		}

		.bt.bt-border:hover {
			background-color: #000;
			color: #fff
		}

		.bt.green-border {
			color: #00be2c;
			border-color: #00be2c
		}

		.bt.green-border:hover {
			color: #000;
			background-color: #00be2c;
			border-color: #00be2c
		}

		.bt.bt-slim {
			padding: 9px 25px;
			-webkit-box-shadow: 0 4px 20px rgba(0, 0, 0, .2);
			box-shadow: 0 4px 20px rgba(0, 0, 0, .2)
		}

		.bt.on {
			background-color: #00be2c
		}

		.bt.on:hover {
			background-color: #00be2c;
			color: #000
		}

		.sprite {
			-webkit-transition-duration: .3s;
			-o-transition-duration: .3s;
			transition-duration: .3s
		}

	}
}
.svg-hide {
	display: none;
}
.fancybox__content {
	border-radius: 16px;
	overflow: hidden;
}  
.fancybox__carousel .fancybox__slide.has-video .fancybox__content {
	padding: 15px;
}
.fancy-hide {
	display: none;
	border-radius: 24px;

	.wehave__carousel-hidden {
		max-width: 630px;
		position: relative;
		background-color: #fff;
		margin: 0 auto;
		border-radius: 16px;
		overflow: hidden
	}

	.wehave__carousel-header {
		/* height: 280px; */
		height: 500px;
	}

	.wehave__carousel-header img {
		width: 100%;
		height: 100%;
		-o-object-fit: cover;
		object-fit: cover
	}

	.wehave__carousel-body {
		padding: 45px 60px
	}

	.wehave__carousel-body h2 {
		text-align: center;
		margin-bottom: 45px
	}

	.wehave__carousel-property .property__item {
		margin-bottom: 15px
	}

	.wehave__carousel-property .property__item-key {
		color: #9c9c9c;
		font-weight: 400
	}

	.wehave__carousel-property .property__item-value {
		border-bottom: 1px solid #000;
		padding: 15px 0;
		line-height: 1.3
	}

	.wehave__list-item {
		margin-bottom: 10px
	}

	.wehave__list-headline {
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: justify;
		-webkit-justify-content: space-between;
		-ms-flex-pack: justify;
		justify-content: space-between;
		-webkit-box-align: center;
		-webkit-align-items: center;
		-ms-flex-align: center;
		align-items: center;
		border-bottom: 1px solid #7a7a7a;
		cursor: pointer;
		margin-bottom: 15px
	}

	.wehave__list-headline.on .wehave__list-close .sprite {
		-webkit-transform: rotate(135deg);
		-ms-transform: rotate(135deg);
		transform: rotate(135deg);
		-webkit-transition-duration: .3s;
		-o-transition-duration: .3s;
		transition-duration: .3s
	}

	.wehave__list-title {
		padding: 5px 0
	}

	.wehave__list-title h3 {
		margin-bottom: 0
	}

	.wehave__list-close {
		width: 33px;
		height: 33px
	}

	.wehave__list-close .sprite {
		width: inherit;
		height: inherit;
		-webkit-transition-duration: .3s;
		-o-transition-duration: .3s;
		transition-duration: .3s;
		vertical-align: baseline
	}

	.wehave__list-text {
		max-height: 0;
		overflow: hidden;
		-webkit-transition-duration: .3s;
		-o-transition-duration: .3s;
		transition-duration: .3s
	}

	.wehave__list-text.on {
		max-height: 2000px;
		-webkit-transition-duration: .3s;
		-o-transition-duration: .3s;
		transition-duration: .3s
	}


}

.yapams {
	background: url("../images/ymaps_bg.png") no-repeat top center/cover transparent;
	
	padding: 200px;

	.details {
		background-color: #000;
		padding: 1.5rem 13rem 1.5rem 2rem;  
		border-radius: 1rem;
		z-index: 2;
		pointer-events: all;
		width: 465px;
	}

	@media only screen and (max-width: 990px) {
		.details {
			padding: 1.5rem;
			width: 100%;
			position: relative;
			margin-bottom: 30px;
			top: 0%;
			transform: translateY(0%);
		}
	}

	.details address {
		font-style: normal;
		margin: 0;
		color: #F7F7F7;
		font-size: 16px;
	}

	.details h2 {
		color: #F7F7F7;
	}

	.details a {
		text-decoration: none;
		opacity: 1;
	}

	.details span#create-route {
		color: #F5E400;
		text-decoration: underline;
		cursor: pointer;
		opacity: 1;
		transition: 0.5s ease opacity;
	}

	.details span#create-route:hover {
		opacity: 0.9;
	}

	.details a.email {
		color: #F7F7F7;
	}

	.details a.email:active {
		color: #F5E400;
	}

	.details a.email:visited {
		color: #F5E400;
	}

	body.neostream section.mapping .map {
		height: 500px;
	}

}
