.footer {

	background-color: #000;
	border-radius: 16px 16px 0 0;
	margin-top: -9px;
	z-index: 2;
	padding: 80px 0;

	&__logo svg {
		height: 37px;
		width: 92px;
	}

	.app > ul {
		margin-top: 25px;
	}

	@media only screen and (max-width: 990px) {
		.app {
			margin-top: 20px;
		}
	}

	.app p {
		color: #F7F7F7;
		color: #F7F7F7;
		text-align: center;
	}

	@media only screen and (max-width: 990px) {
		.app p {
			text-align: left;
		}
	}

	.app ul {
		display: flex;
		flex-direction: row;
		padding: 0;
		justify-content: center;
	}

	@media only screen and (max-width: 990px) {
		.app ul {
			justify-content: left;
		}
	}

	.app ul li:not(:last-child) {
		padding-right: 30px;
	}

	.app ul li svg {
		width: 30px;
		height: 30px;
	}

	.footer__ul ul li a {
		display: block;
		color: white;

		&:hover {
			color: $color-yellow;
		}
	}

	@media only screen and (max-width: 990px) {
		.footer__ul ul li a {
			padding: 8px 0;
		}
	}
}